<template>
  <div class="el-card">
    <div class="el-card__header" v-if="$slots.header">
      <slot name="header">{{ header }}</slot>
    </div>
    <div class="el-card__body" :style="bodyStyle">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'el-card',

    props: ['header', 'bodyStyle']
  };
</script>



